<template>
    <div>
        <template v-for="(item,index) in menuList">
            <Dropdown placement="right-start" v-if=" item.children != undefined ">
                <DropdownItem>{{item.title}}<Icon type="ios-arrow-forward" /></DropdownItem>
                <DropdownMenu slot="list">
                    <vc-side-dropdown-menu :menu-list="item.children"></vc-side-dropdown-menu>
                </DropdownMenu>
            </Dropdown>
            <DropdownItem :name="item.path" :ref="item.path" v-else>{{item.title}}</DropdownItem>
        </template>
    </div>
</template>
<style scoped>
.ivu-icon-ios-arrow-forward{ font-size: 14px;}
</style>

<script>
export default {
    name: 'vc-side-dropdown-menu',
    props:['menuList']
}
</script>
