<template>
    <div>
        <template v-if="!sideCollapse">
            <template v-for="(item,index) in menuList">
                <Submenu :name="item.id" v-if=" item.children != undefined ">
                    <template slot="title">
                        <Icon :type="item.icon" v-if=" item.icon != undefined " />
                        {{item.title}}
                    </template>
                    <vc-side-menu-item :side-collapse="sideCollapse" :menu-list="item.children"></vc-side-menu-item>
                </Submenu>
                <MenuItem  :name="item.id"  :to="{ path:item.path }" v-else><Icon :type="item.icon" v-if=" item.icon != undefined " />{{item.title}}</MenuItem>
            </template>
        </template>
        <template v-else>
            <template v-for="(item,index) in menuList">
                <Dropdown placement="right-start" v-if=" item.children != undefined " @on-click="toPath" >
                    <MenuItem :name="item.id"><Icon :type="item.icon" v-if=" item.icon != undefined " /></MenuItem>
                    <DropdownMenu slot="list">
                        <vc-side-dropdown-menu :menu-list="item.children"></vc-side-dropdown-menu>
                    </DropdownMenu>
                </Dropdown>
                <Tooltip :content="item.title" placement="right" v-else>
                    <MenuItem :name="item.id" :to="{ path:item.path }" ><Icon :type="item.icon" v-if=" item.icon != undefined " /></MenuItem>
                </Tooltip>
            </template>
        </template>
    </div>
</template>
<script>
import sideDropdownMenu from './sideDropdownMenu.vue'

export default {
    name: 'vc-side-menu-item',
    props:['sideCollapse','menuList'],
    components:{
        'vc-side-dropdown-menu':sideDropdownMenu
    },
    data() {
        return {
        }
    },
    methods:{
        toPath(path){
            this.$router.push({ path:path })
        }
    }
}
</script>

